@import url(https://fonts.googleapis.com/css?family=RobotoDraft:500);

*{
  font-family: 'RobotoDraft', sans-serif;
  font-size: 17px;
}

body{
  padding: 60px;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}
.gif-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.search-container{
  margin-left: 3%;
}
